body {
  background-image: url("https://cdn.pixabay.com/photo/2018/05/01/07/52/tuscany-3364921_1280.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.clock {
  width: 560px;
  height: 560px;
  background-color: rgba(255, 255, 255, 0);
  background-image: url("https://cdn.pixabay.com/photo/2016/10/01/10/23/tuscany-1707192_1280.jpg");
  background-size: cover;
  border-radius: 50%;
  position: relative;
  top: auto;
  left: 280px;
}

.clock-arrow {
  transform-origin: bottom;
  background-color: #000;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  position: absolute;
  transform: translate(-50%);
}

.clock-center {
  background-color: #000;
  position: absolute;
}

.calendar {
  flex-direction: row;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 600;
  display: flex;
}

.date-style {
  flex-direction: row;
  display: flex;
}

